import React from 'react';
import {BrowserRouter, Link} from "react-router-dom";





const Header = ({Header}) => {
    return (
        <div>
            <BrowserRouter>
                <ul id="navbar">
                    <li><a href="/users"></a></li>
                    <li><a href="/note"></a></li>
                    <li><a href="#"></a>
                        <ul>
                            <li><a href="#"></a></li>
                            <li><a href="#"></a></li>
                            <li><a href="#">Email</a></li>
                        </ul>
                  </li>
                  <li><a href="#"></a></li>
                  </ul>
            </BrowserRouter>
        </div>
    );
}

export default Header;



